import React, {useEffect} from 'react'
import './measurement-gen.css'
import {getCircuits} from './../server-calls/server-calls'
import { useDispatch, useSelector } from 'react-redux'
import { UPDATE_CIRCUIT_DETAILS, UPDATE_CIRCUIT_SELECTION } from './measurement-gen-slice'
import { RESET_PROMPT_CODE_PAGE } from './code-edit-run-slice'

import CodeEditRun from './code-edit-run'
import TextPagination from './../../component/text-pagination/text-pagination'

function MeasurementGenerator() {

    const circuitNames = useSelector((state) => state.measGen.circuitNames)
    const activeCircuitIndex = useSelector((state) => state.measGen.activeCircuitIndex)
    const activeUID = useSelector((state) => state.measGen.activeUID)
    const activeDiagram = useSelector((state) => state.measGen.activeDiagram)
    const activeDescription = useSelector((state) => state.measGen.activeDescription)
    const activeExamplePrompt = useSelector((state) => state.measGen.activeExamplePrompt)
    const dispatch = useDispatch()
    
    useEffect(
        () => {
            getCircuits(onRecievingCircuitDetails)
        },[]
      )
    
    // This function is called to reset the Prompt page when active circuit is changed
    useEffect(
        () => {
            dispatch(RESET_PROMPT_CODE_PAGE({promptPlaceholder: activeExamplePrompt, promptUID: activeUID}))
        },[activeUID, activeExamplePrompt]
      )

    const onRecievingCircuitDetails = React.useCallback((data) => {
        dispatch(UPDATE_CIRCUIT_DETAILS(data))
    })

    const onCircuitSelection = React.useCallback((event) => {
        dispatch(UPDATE_CIRCUIT_SELECTION(event.target.textContent))
    })

    return (
        <div className='page'>
            <div className='page-header'>Measurement Generator</div>
            <div className='measurement-gen'>
                <div className='input-space'>
                    <div className='circuit'>
                        {(activeDiagram==="")?<p className='page-decription'>Server is down</p> :<img src={activeDiagram} alt="circuit"/>}
                    </div>
                    <p className='page-decription'>Select one of the below circuits to play with. If you would like to suggest any new circuits to play with or provide feedback on any of the existing circuits or generated code, please email to navin@soliotntech.com</p>
                    <TextPagination pageNames={circuitNames} selectedIndex={activeCircuitIndex} onChange={onCircuitSelection}/>
                    <p className='page-decription'>{activeDescription}</p>
                </div>
                <div className='code-editor'>
                    <CodeEditRun/>
                </div>
            </div>
        </div>
    )
}

export default MeasurementGenerator