import { createSlice } from '@reduxjs/toolkit';


// Define Initial State
const initialState = {
    circuitDetails : [],
    circuitNames : [],
    activeCircuitIndex: -1,
    activeUID: "",
    activeDiagram: "",
    activeDescription: "",
    activeExamplePrompt: ""
}

// Slicer
export const measurement_gen_slice = createSlice({
    name: "meas-gen",
    initialState,
    reducers: {
        UPDATE_CIRCUIT_DETAILS: (state, action) => {
            state.circuitDetails = action.payload

            if(state.circuitDetails === []) {
                state.circuitNames = []
                state.activeCircuitIndex = -1
                state.activeUID = ""
                state.activeDiagram = ""
                state.activeDescription = ""
                state.activeExamplePrompt = ""
            }
            else {
                state.circuitNames = state.circuitDetails.map((element) => element.name)
                state.activeCircuitIndex = 0
                const activeCircuitDetail = state.circuitDetails.at(state.activeCircuitIndex)
                state.activeUID = activeCircuitDetail.uid
                state.activeDiagram = activeCircuitDetail.diagram
                state.activeDescription = activeCircuitDetail.description
                state.activeExamplePrompt = activeCircuitDetail.example_prompt
            }

        },

        UPDATE_CIRCUIT_SELECTION: (state, action) => {
            try {
                const circuitName = action.payload
                const isMatch = (element) => element===circuitName
                state.activeCircuitIndex = state.circuitNames.findIndex(isMatch)
                const activeCircuitDetail = state.circuitDetails.at(state.activeCircuitIndex)
                state.activeUID = activeCircuitDetail.uid
                state.activeDiagram = activeCircuitDetail.diagram
                state.activeDescription = activeCircuitDetail.description
                state.activeExamplePrompt = activeCircuitDetail.example_prompt
            }
            catch(err) {
                console.log(err)
            }

        },
    }
});

// Export the Actions & Reducers
export const { UPDATE_CIRCUIT_DETAILS, UPDATE_CIRCUIT_SELECTION } = measurement_gen_slice.actions;
export default measurement_gen_slice.reducer