import React from "react";
import './text-pagination.css'


function TextPagination(props) {
    const items = props.pageNames
    const selected_item_index = props.selectedIndex
    const onChangeFunction = props.onChange

    return (
        <div className="text-pagination">
            {items.map((element, index) => {
                if (index === selected_item_index) {
                    return <p className="text-pagination-element text-pagination-element-selected" key={index}>{element}</p>
                }
                else {
                    return <p className="text-pagination-element text-pagination-element-unselected" key={index} onClick={onChangeFunction}>{element}</p> 
                }
            })}
        </div>
    )
}

export default TextPagination