import { createSlice } from '@reduxjs/toolkit';


// Define Initial State
const defaultEditorValue = ''
const defaultPromptPlaceholder = "Type the prompt in here..."

const initialState = {

    promptValue: "",
    promptPlaceholder: defaultPromptPlaceholder,
    promptUID: "",


    // Get the initial State from the localStorage
    //editorValue: localStorage.getItem('editorValue') || '',
    editorValue: defaultEditorValue,
    editorState: "",
    editEnabled: false,

    codeFeedback: "none",
    
    serverWaiting: false
}

// Slicer
export const code_edit_run_slice = createSlice({
    name: "code-edit-run",
    initialState,
    reducers: {
        RESET_PROMPT_CODE_PAGE: (state, action) => {
            try {
                state.promptPlaceholder = action.payload.promptPlaceholder
                state.promptUID = action.payload.promptUID
            }

            catch(err) {
                state.promptPlaceholder = defaultPromptPlaceholder
                state.promptUID = ""
            }
            state.promptValue = ""
            state.editorValue = defaultEditorValue
            state.editEnabled = true
            state.codeFeedback = "none"
            state.serverWaiting = false
        },

        UPDATE_PROMPT_VALUE: (state, action) => {
          state.promptValue = action.payload  
        },

        UPDATE_EDITOR_VALUE: (state, action) => {
            state.editorValue = action.payload.value
            state.editorState = action.payload.viewUpdateState
        },

        UPDATE_EDITOR_STATE: (state, action) => {
            state.editEnabled = action.payload
        },

        UPDATE_FEEDBACK: (state, action) => {
            state.codeFeedback = action.payload
        },

        UPDATE_SERVER_WAITING_STATUS: (state, action) => {
            state.serverWaiting = action.payload
        },
    }
});

// Export the Actions & Reducers
export const { RESET_PROMPT_CODE_PAGE, UPDATE_PROMPT_VALUE, UPDATE_EDITOR_VALUE, UPDATE_EDITOR_STATE, UPDATE_FEEDBACK, UPDATE_SERVER_WAITING_STATUS } = code_edit_run_slice.actions;
export default code_edit_run_slice.reducer