const getCircuits = async(onRecievingCircuitDetails) => {
    try {
        const response = await fetch(BASE_URL+"/circuits")
        const myJSobj = await response.json()
        if(myJSobj.status === true) {
            //alert("Connection is good!!!") 
            onRecievingCircuitDetails(myJSobj.data)
        }
        else {
            //alert("Connected!! But BAD status") 
        }
    }
    catch(err) {
        console.log(err)
    }
}

const postGenerateProgram = async(uid, prompt, onRecievingProgramDetails) => {
    try {
        const response = await fetch(BASE_URL+"/generateprogram", {
            method: 'POST',
            headers: { 'Accept': 'application/json', 'Content-Type':'application/json'},
            body: JSON.stringify({uid: uid, prompt: prompt})
        })
        const myJSobj = await response.json()
        if(myJSobj.status === true) {
            //alert("Connection is good!!!") 
            onRecievingProgramDetails(myJSobj.data)
            console.log(myJSobj)
        }
        else {
            //alert("Connected!! But BAD status") 
            onRecievingProgramDetails("# Server call is successfull but response data is bad")
        }
    }
    catch(err) {
        console.log(err)
        onRecievingProgramDetails("# Error when calling the server")
    }
}

